.container {
    padding: var(--spacing-6);
    max-width: 1400px;
}

.sectionTitle {
    font-size: var(--font-size-lg);
    font-weight: 600;
    color: var(--color-white);
    margin: 0 0 var(--spacing-4) 0;
}

/* Metrics Section */
.metricsSection {
    margin-bottom: var(--spacing-6);
}

.metricsGrid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: var(--spacing-4);
}

@media (max-width: 1024px) {
    .metricsGrid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 640px) {
    .metricsGrid {
        grid-template-columns: 1fr;
    }
}

.metricCard {
    text-align: center;
}

.metricValue {
    font-size: var(--font-size-3xl);
    font-weight: 700;
    color: var(--color-accent);
    margin-bottom: var(--spacing-1);
}

.metricLabel {
    font-size: var(--font-size-sm);
    color: var(--color-white);
    margin-bottom: var(--spacing-2);
}

.metricSubtext {
    font-size: var(--font-size-xs);
    color: var(--color-success);
}

/* Chart Section */
.chartSection {
    margin-bottom: var(--spacing-6);
}

.chartTitle {
    font-size: var(--font-size-lg);
    font-weight: 600;
    color: var(--color-white);
    margin: 0 0 var(--spacing-5) 0;
}

/* Line Chart */
.lineChart {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    height: 200px;
    padding-bottom: var(--spacing-8);
    position: relative;
}

.linePoint {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    position: relative;
}

.point {
    width: 12px;
    height: 12px;
    background: var(--color-accent);
    border-radius: 50%;
    position: absolute;
    cursor: pointer;
    z-index: 2;
    transition: transform var(--transition-fast);
}

.point:hover {
    transform: scale(1.3);
}

.point:hover .tooltip {
    opacity: 1;
    visibility: visible;
}

.tooltip {
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    padding: var(--spacing-1) var(--spacing-2);
    background: var(--color-primary-bg-dark);
    border-radius: var(--radius-md);
    font-size: var(--font-size-xs);
    color: var(--color-white);
    white-space: nowrap;
    opacity: 0;
    visibility: hidden;
    transition: all var(--transition-fast);
    margin-bottom: var(--spacing-2);
}

.dateLabel {
    position: absolute;
    bottom: -28px;
    font-size: var(--font-size-xs);
    color: var(--color-gray-400);
    white-space: nowrap;
}

/* Two Column */
.twoColumn {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: var(--spacing-5);
    margin-bottom: var(--spacing-6);
}

@media (max-width: 1024px) {
    .twoColumn {
        grid-template-columns: 1fr;
    }
}

/* Category List */
.categoryList {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-4);
}

.categoryRow {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-2);
}

.categoryHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.categoryName {
    font-size: var(--font-size-sm);
    font-weight: 500;
    color: var(--color-white);
}

.categoryRevenue {
    font-size: var(--font-size-sm);
    font-weight: 600;
    color: var(--color-accent);
}

.categoryBarBg {
    height: 8px;
    background: var(--color-primary-bg-dark);
    border-radius: var(--radius-full);
    overflow: hidden;
}

.categoryBarFill {
    height: 100%;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-pink) 100%);
    border-radius: var(--radius-full);
    transition: width 0.5s ease;
}

.categoryMeta {
    display: flex;
    justify-content: space-between;
    font-size: var(--font-size-xs);
    color: var(--color-gray-500);
}

/* Leaderboard */
.leaderboard {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-3);
}

.leaderboardItem {
    display: flex;
    align-items: center;
    gap: var(--spacing-4);
    padding: var(--spacing-3);
    background: rgba(255, 255, 255, 0.02);
    border-radius: var(--radius-lg);
}

.rank {
    width: 28px;
    height: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--color-primary-bg-dark);
    border-radius: var(--radius-md);
    font-size: var(--font-size-sm);
    font-weight: 600;
    color: var(--color-gray-400);
}

.topRank {
    background: rgba(184, 224, 63, 0.2);
    color: var(--color-accent);
}

.leaderboardItem:first-child .rank {
    background: rgba(251, 191, 36, 0.2);
    color: var(--color-warning);
}

.merchantInfo {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.merchantName {
    font-weight: 500;
    color: var(--color-white);
}

.merchantStats {
    font-size: var(--font-size-xs);
    color: var(--color-gray-500);
}

.merchantRevenue {
    text-align: right;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: var(--spacing-1);
}

.revenueValue {
    font-weight: 600;
    color: var(--color-accent);
}

.ratingBadge {
    font-size: var(--font-size-xs);
    color: var(--color-warning);
}

/* Breakdown Section */
.breakdownSection {
    margin-bottom: var(--spacing-6);
}

.breakdownHeader {
    padding: var(--spacing-5);
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.breakdownTable {
    overflow-x: auto;
}

.tableHeader,
.tableRow {
    display: grid;
    grid-template-columns: 1.5fr repeat(5, 1fr);
    gap: var(--spacing-4);
    padding: var(--spacing-4) var(--spacing-5);
}

.tableHeader {
    background: var(--color-primary-bg-dark);
    font-size: var(--font-size-sm);
    font-weight: 600;
    color: var(--color-gray-400);
}

.tableRow {
    border-bottom: 1px solid rgba(255, 255, 255, 0.03);
    font-size: var(--font-size-sm);
    color: var(--color-gray-300);
}

.tableRow:last-child {
    border-bottom: none;
}

.revenueCell {
    color: var(--color-accent);
    font-weight: 500;
}

.successCell {
    color: var(--color-success);
}

.warningCell {
    color: var(--color-warning);
}